      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.logo-urbo-container p {
  margin: 0px auto;
  margin-top: 22px;
}

.logo-vikua-container img {
  width: 100%;
  margin-top: 30px;
  height: auto;
